    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La elección del método de recolección de retroalimentación en contextos turísticos con enfoque étnico debe responder tanto a las características culturales de las comunidades anfitrionas como a las preferencias y capacidades de los visitantes. Los métodos digitales ofrecen eficiencia y alcance global; los físicos, cercanía y accesibilidad; y los verbales, una conexión profunda basada en el diálogo y la tradición oral. Integrar estas tres modalidades de manera complementaria permite obtener información diversa y significativa, fortaleciendo la participación comunitaria, la autenticidad del intercambio intercultural y la mejora continua de los servicios turísticos bajo principios de respeto, inclusión y sostenibilidad.


